import React from 'react';
import axios from 'axios';


export default () => {

    const getPostData = () => {
        axios.post('/info.do',{
            data:{
                id: 12345
            }
          })
    }

    const getData = () => {
        axios.post('/user.do',{
            params:{
              ID:12345
            }
          })
    }

    return <div>
        <button onClick={getPostData}>post</button>
        <button onClick={getData}>get</button>
    </div>
}